body {
  background-color: rgb(195, 149, 247);
}

.main__container{
  width: 100%;
}

.search__box {
    width: 60%;
    border-radius: 30px;
    height: 30px;
    background-color: rgb(121, 161, 248);
    font-size: 20pt;
    font-family: cursive;
    padding: 0 30px;
}

.search__box__container {
    text-align: center;
    margin-top: 5px;
}

.video_container {
  /* background-color: red; */
  width: 100%;
  height: 500px;
  margin: 10px 10px;
}


.video_box {
  height: 500px;
  width: 1280px;
  /* background-color: green; */
  display: flex;
  align-items: center;
  margin: 0 auto;
  overflow: hidden;
  scroll-behavior: smooth;
}

@media (max-width: 1280px) {
  .video_box {
    width: 960px;
  }
}

@media (max-width: 960px) {
  .video_box {
    width: 640px;
  }
}

@media (max-width: 640px) {
  .video_box {
    width: 320px;
  }
}

.buttons_container {
  height: 35px;
  width: 30%;
  box-align: center;
  /* background-color: white; */
  margin: 5px auto;
  display: flex;
  justify-content: center;
  padding: 5px 0;

}

.buttons {
  height: 30px;
  width: 30px;
  background-color: lime;
  border-radius: 30px;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-user-select: none;
}

.hidden {
  visibility: hidden;
}

.button_current {
  user-select: none;
}

.grab {
  cursor: grabbing;
}

#hiddenButton1, #hiddenButton2 {
  background-color: rgb(48, 197, 140);
  border-radius: 15%;
  height: 25px;
  width: 25px;
}
