.clip_container {
  height: 500px;
  width: 300px;
  margin:5px 10px;
  background-color: rgb(126, 197, 245);
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;

}

.pic_container {
  height: 160px;
  width: 100%;
  overflow: hidden;
  background-size: cover;
}

.pic_field {
  object-fit: contain;
  /* поведение картинки - заполнение */
  overflow: hidden;
  /* обрезает лишнее за границами внешнего блока */
}


.link_field {
  height: 60px;
  width: 90%;
  /* background-color: rgb(235, 35, 35); */
  color: rgb(78, 74, 74);
  text-align: center;
  font-weight: bold;
  font-size: 11pt;
  text-decoration: none;
  margin: 0 auto;
}

.channel_field {
  margin-top: 5px;
  height: 20px;
  /* background-color: rgb(22, 56, 121); */
}

.date_field {
  height: 20px;
  /* background-color: rgb(31, 185, 77); */
}

.views_field {
  height: 20px;
  /* background-color: rgb(221, 29, 125); */
}

.description_field {
  height: 160px;
  /* background-color: rgb(81, 5, 83); */
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 20px;
}
